<template><div class="content"><h1 class="post-title">体制外的人<a class="header-anchor" id="%E4%BD%93%E5%88%B6%E5%A4%96%E7%9A%84%E4%BA%BA" href="#%E4%BD%93%E5%88%B6%E5%A4%96%E7%9A%84%E4%BA%BA">#</a></h1><div class="metabar"><div class="metabar-item">2021/12/19</div><div class="metabar-item">思想</div><div class="metabar-item">约 15 分钟读完</div></div>
<blockquote>
<p>These walls are kind of funny like that. First you hate them, then you get used to them. Enough time passed, you get so you depend on them. That's <em><strong>institutionalizing</strong></em>．<br></p>
</blockquote>
<p>这是出自《肖申克的救赎》的很著名的一段话，译文是</p>
<blockquote>
<p>这些墙很奇怪，起初你讨厌它，然后你开始适应它；时候够了，你就变得十分依赖于它了。这就叫做 <strong>「体制化」</strong>。</p>
</blockquote>
<p>要理解这个<strong>体制化</strong>的含义，首先要明白什么是体制，以及体制化对于我们而言的意义是什么，给我们带来的是好处还是坏处。在这篇文章里，我会结合我的现实感触和思考来解答这些问题。</p>
<p>首先，体制本意是规则（还有其它含义，与本文无关），一套规范。从体制化（institutionalizing）这个词语来理解，其相较于一般的规则化（normalization）多了一种人为的组织和结构，也正因此它指的是一种人为的、固有的规则。</p>
<p>回到上面那段话中。那段话描述的实际上是在监狱里生活从生疏到适应的过程。监狱的生活与外界的生活很不一致，于是在你进入这个环境的时候你会本能地产生排斥。然而由于在监狱里生活是不可改变的事实，所以随着时间的推移你被迫适应于这样一种环境。直到最后彻底依赖于这种环境并不愿意离开，因为你会认为离开这个环境以后自己就没了存在的意义，这个时候就可以说<strong>被体制化了</strong>。也正因此，在《肖申克的救赎》末尾，从监狱释放出来的人放弃了自由地活着而选择了自杀。</p>
<p>你不能理解他的行为也没关系，毕竟你（大概率）没有坐过牢，我也没有。这是电影对体制化效果的合理阐释，仅此而已。</p>
<p>在上面的论述中也不难发现，这个体制化的概念其实不仅限于对监狱生活或者其他「新生活」的适应，而可以适用于<strong>任何</strong>环境对我们的意识习惯造成的影响。</p>
<h2>体制化与被环境影响的区别<a class="header-anchor" id="%E4%BD%93%E5%88%B6%E5%8C%96%E4%B8%8E%E8%A2%AB%E7%8E%AF%E5%A2%83%E5%BD%B1%E5%93%8D%E7%9A%84%E5%8C%BA%E5%88%AB" href="#%E4%BD%93%E5%88%B6%E5%8C%96%E4%B8%8E%E8%A2%AB%E7%8E%AF%E5%A2%83%E5%BD%B1%E5%93%8D%E7%9A%84%E5%8C%BA%E5%88%AB">#</a></h2>
<p>体制化究竟给我们带来了什么？我们必须清楚它与一般的环境影响人有什么区别。</p>
<p>环境对人的影响，体现在人在面对环境时，结合自己的主观判断和经验所作出的反应和新的经验的积累。例如你读了一本书，那么这本书带给你的不仅仅是它原本的、朴素的文字内容，而且还有你结合自己过往学识和经历所得来的对自己而言有用处的思考。你对环境的反馈，很明显，思考和行为的主动权在你自己的手上。你既可以不反应，也可以做出自己认为合理，对自己有利的反应。</p>
<p>而体制化实际上是一种固化的思维或行动模式的传递，是经由<strong>多数人</strong>（注意，一定是多数人，否则它的影响力就不至于造成体制化）的认可以后直接传递给你，思考和决定的主动权并不在你的身上。为什么在这里还要强调主动权呢？因为往往体制性的东西在进入你的思想的时候是很难引起你的质疑的（即很多人认为这样的东西是应该无条件接受的），除非你是一个有着强烈批判性思维的人。这也是为什么说批判性思维以及唯物地看待世间万物很重要。</p>
<p>所以如果你没有批判性思维，或者持续地处于一种「觉得自己无知，应该被浇灌」而<strong>随意接受</strong>的状态下，就很容易被这些「群体信仰」所俘获，成为他们的一员，即体制化。</p>
<p>我们对体制化的概念可以总结为，<strong>体制化是个体的空白思想被原有的、固化的思维或行动模式置换。</strong> 在这里，固化是指长期保持不变的。固化的事物往往都有其固化的原因。个人认为，在唯物领域内，唯有客观真理具有固化的合理性，而思想范畴内的固化大抵都是体制化。任何经过严格证明和检验的科学知识、客观定律的传递都不能叫做体制化。</p>
<h2>体制化会给我们带来坏处吗？<a class="header-anchor" id="%E4%BD%93%E5%88%B6%E5%8C%96%E4%BC%9A%E7%BB%99%E6%88%91%E4%BB%AC%E5%B8%A6%E6%9D%A5%E5%9D%8F%E5%A4%84%E5%90%97%EF%BC%9F" href="#%E4%BD%93%E5%88%B6%E5%8C%96%E4%BC%9A%E7%BB%99%E6%88%91%E4%BB%AC%E5%B8%A6%E6%9D%A5%E5%9D%8F%E5%A4%84%E5%90%97%EF%BC%9F">#</a></h2>
<p>不一定。因为<strong>被体制化</strong>（institutionalized）是一种十分普遍的现象，许多人一生所信奉的观点也都是体制性的，但这丝毫不影响他们的生活，只是面对有些话题和选择显得无知了点，寂寞了点，以至于莽撞了点，暴力了点。</p>
<p>体制化所带来的最为轻微的影响，是人与人思想的不相容。一个具有强烈批判性思维的人，能够接受哪怕最为荒诞不经、经不起推敲的理论进入他的脑海，被他的逻辑一步步推敲，只是路径长短的区别。而它的逻辑都是基于客观世界的。被体制化的一个很大的特点便是它存在着一种无形的束缚，将人的思想和行为约束在一定的范围内。这一范围可大可小，但是很难跳脱。<strong>有些人的所谓底线也是从这里来的</strong>，但实际上这并不是真正的「底线」所应有的概念，而更像是一种由体制化信息和自我认知所杂糅、发酵的动态产物。</p>
<p>如何判断自己是否被体制化？你可以思考以下几个问题：</p>
<ul>
<li>你是否认为某些问题不需要有答案，某些行为不需要理由？</li>
<li>你是否经常排斥其它的观念？在这里，经常不指时间，而是「见一次排斥一次」的意思。</li>
<li>你是否经常将不是自己思考的结果作为对重要结论的唯一或者权威论据？</li>
<li>等等。</li>
</ul>
<p>其实是否被体制化很好判断，只要理解了概念。比如，通过概念和推理可以知道，被高度体制化的人会满口空话（这是因为大多数不是经过自己思考的内容）、盲目自大（这是因为自认为自己的认知体系十分丰富与满足），总是有着某种怪异的信仰与执着（与前一个原因类似，自己该做的与不该做的事情之间的界限不是由自己划定的）和飘忽不定的底线（见上文），等等。</p>
<div class="notice warning"><p><strong>注意</strong></p>
<p>我们在当今社会能够正常地、自由地活动的前提是遵循法律和社会规则与道德，这在某种程度上而言也算是一种体制化，然而它并不是本文的叙述对象，因为法律、规则等实际上是一种人为规定的、与上文提到的客观真理等具有类似的性质的存在。</p>
</div>
<h2>体制外的人<a class="header-anchor" id="%E4%BD%93%E5%88%B6%E5%A4%96%E7%9A%84%E4%BA%BA" href="#%E4%BD%93%E5%88%B6%E5%A4%96%E7%9A%84%E4%BA%BA">#</a></h2>
<p>这里的体制不指任何政治含义。</p>
<p>要想要以最理性的方式看待这个世界，就需要成为体制外的人（不是规则外的人）。对于这样做的意义，对不同人也不尽相同。你可能认为自己当前的思想观念和意识形态具有足够的完备性，没有必要对其进行批判或者质疑，那么顺其自然其实也是一个不错的选择。只是理性地看待这个世界能够便于我们以更长远的眼光和更大的深度来看待事物和对事物发表评价，也就是能把事看得更透，更懂，而不是吹吹牛唠唠嗑就结束。这样做，又有利于我们长期的自我学习和完善。</p>
<p>正如上文所说，体制内的人往往意识不到自己内心的真实状态，有的时候甚至不知道自己究竟想要做什么。所以，体制外与体制内的人很容易产生冲突，比如指点对方故作清高，有时牵涉到与文化传统或者民族信仰等内容的话题时，也会引出一系列与思想正确性和历史有关的「有关痛痒」的议题。究竟谁对谁错，孰优孰劣？想必这并不是一个有价值的问题，因为前文已经说过，是否被体制化对于不同人的意义不一样，自然不能对其进行这样的对比。</p>
<p>要知道，我们存活在这个世界上，本质上是为了自己。你无法反驳这一点，因为这是生物的本能。如果你十分坚定地认为，自己一定要为了他人而去死，而去牺牲自己的利益，这也与「为了自己」不冲突，因为你只是在做自己想做的事情，并且你会一直努力去达到这一点。「为了自己」与「为了自己的利益」不能一概而论。</p>
<p>那么既然是为了自己，对自己价值和思想境界理应达到的高度的衡量，自然也要以对自己有利为准绳，而不是日夜操持着「观念不正」云云，借由自己对自己脑海中存在的体制性思想的不完全认知去批判那些自己根本一点也不了解的人。</p>
<p>体制外的人总是能够接受与它截然不同的观点的。经常有一句话会说，一个人在知识和思想并不成熟之时往往会很狭隘，因为没见过世面，没见过与其相反的观点。在这里我们可以说，这个所谓的「知识和思想的成熟」，其实就是跳脱于自己当前所在的体制，而去做一个体制外的人。</p>
</div></template>